@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  border: none;
  outline: none;
  background: none;
  text-decoration: none; }

$main: #7775E4;
$pri: #BFBEFF;
$dark: #000000;
$light: #FFFFFF;
$para: #333333;

@mixin flex($justify) {
  display: flex;
  justify-content: $justify;
  align-items: center; }

@mixin grid($columns) {
  display: grid;
  grid-template-columns: $columns;
  grid-gap: 20px; }

@mixin image() {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

@mixin shrinkLabel() {
  top: -12px;
  left: 5px;
  transform: translateY(-50%) scale(.8); }

@mixin btnHover() {
  // transform: scale(1.02) translateY(-1px)
  background-color: #6966ff;
  box-shadow: 0 0 4px rgba(#6966ff, .2); }

@mixin trans() {
  transition: all .5s cubic-bezier(0,1,0,1); }

/**********    bg-for-close    **********/
.bg-for-close {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba($dark, .01);
  z-index: 2; }

/**********    mobile-popup    **********/
.mobile-popup {
  position: absolute;
  top: 40px;
  right: 5px;
  background-color: $light;
  padding: .5rem 1rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba($dark, .2);
  pointer-events: none;
  opacity: 0;
  z-index: 10;
  transition: all .45s cubic-bezier(0,1,0,1);

  &.active {
    top: 60px;
    opacity: 1;
    pointer-events: auto; }

  .pointer {
    position: absolute;
    right: 15px;
    top: -3px;
    transform: rotate(45deg);
    height: 10px;
    width: 10px;
    border-radius: 2px;
    background-color: $light; }

  .inner {
    cursor: pointer;
    position: relative;
    z-index: 10;

    ul {
      li {
        @include flex($justify: center);
        line-height: 3rem;
        border-bottom: 1px solid rgba($dark, .2);

        &:nth-last-child(1) {
          border-bottom: none; }

        &:hover {
          svg {
            path {
              fill: $main; } }

          span {
            color: $main; } }

        svg {
          path {
            fill: $dark; } }

        span {
          font-size: 18px;
          margin-right: 5px; } } } } }

/**********    loading    **********/
.loading {
  height: 100%;
  width: 100%;
  @include flex($justify: center);

  &.fullPage {
    background-color: #292929;
    height: 100vh; }

  .loading-area {
    position: relative;
    height: 50px;
    width: 50px;
    @include flex($justify: center);
    border-radius: 50%;
    animation: spin 2s ease-in-out infinite;

    @keyframes spin {
      0% {
        transform: rotate(0deg); }

      100% {
        transform: rotate(360deg); } }

    p {
      position: absolute;
      left: 50%;
      top: 150%;
      transform: translate(-50%, -50%);
      color: $light; }

    .circle {
      position: absolute;
      height: 100%;
      width: 100%;
      border-radius: 50%;
      animation: rotate 1s ease-in-out infinite;

      @keyframes rotate {
        0% {
          transform: translate(-50%, -50%) rotate(0deg);
          // border-color: $main
          border-color: #FF9900; }

        25% {
          transform: translate(-50%, -50%) rotate(90deg);
          // border-color: #8c00ff
          border-color: #FFE600; }

        50% {
          transform: translate(-50%, -50%) rotate(180deg);
          // border-color: #00e1ff
          border-color: #DBFF00; }

        75% {
          transform: translate(-50%, -50%) rotate(270deg);
          // border-color: #3bfd00
          border-color: #ADFF00; }

        100% {
          transform: translate(-50%, -50%) rotate(360deg);
          // border-color: #ffe600
          border-color: #70FF00; } }

      &:nth-child(1) {
        left: 20%;
        top: 20%;
        transform: translate(-50%, -50%);
        border-left-width: 3px;
        border-left-style: solid; }

      &:nth-child(2) {
        left: 80%;
        top: 20%;
        transform: translate(-50%, -50%);
        border-right-width: 3px;
        border-right-style: solid;
        animation-delay: .2s; }

      &:nth-child(3) {
        left: 50%;
        top: 80%;
        transform: translate(-50%, -50%);
        border-bottom-width: 3px;
        border-bottom-style: solid;
        animation-delay: .1s; } } } }

/**********    app    **********/
.app {
  @include image();
  padding: .3rem 0;
  min-height: 100vh; }

/**********    wrapper    **********/
.wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 2rem;

  &.noSpace {
    max-width: auto;
    padding: 0; } }

/**********    section    **********/
section {
  padding: 0 20px;

  padding-bottom: 3rem;

  &.no-padding {
    padding: 3rem 0; } }

.section {
  @include flex($justify: center);
  align-items: flex-start;
  margin-top: 6px; }

/**********    custom-button    **********/
.custom-button {
  height: 40px;
  padding: 0 1.5rem;
  @include flex($justify: center);
  border-radius: 10px;
  font-size: 16px;
  color: $light;
  background-color: $main;
  cursor: pointer;
  @include trans();

  &:hover {
    @include btnHover();

    svg {
      transform: translateX(10px); } }

  svg {
    transform: translateX(5px);
    @include trans(); } }

/**********    header    **********/
header {
  @include grid($columns: 1fr 1fr 1fr);
  background-color: rgba($dark, .45);
  backdrop-filter: blur(30px);
  padding: .5rem;
  border-radius: 20px;
  position: sticky;
  top: 0;
  z-index: 10;

  .logo {
    height: 60px;
    width: 60px;

    img {
      width: 100%; } }

  .search {
    @include flex($justify: center);

    form {
      height: 50px;
      max-width: 300px;
      width: 100%;
      border-radius: 20px;
      background-color: rgba($light, .10);
      position: relative;

      input {
        width: 100%;
        height: 100%;
        padding-left: 3rem;
        color: $light;
        font-size: 18px;

        &::placeholder {
          color: rgba($light, 8); } }

      .search-btn {
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        height: 28px;
        width: 28px;
        @include flex($justify: center); } } }

  .das {
    height: 20px;
    width: 1px;
    background-color: rgba($light, .4);
    margin: 0 1rem; }

  .sign-up-btn {
    @include flex($justify: flex-end);

    .icon {
      height: 31px;
      width: 31px;
      border-radius: 50%;
      background-color: $dark;
      @include flex($justify: center);
      margin-right: 10px;
      pointer-events: none; }

    span {
      font-size: 18px;
      color: $light;
      @include flex($justify: center);

      &.su {
        text-decoration: underline; }

      svg {
        margin-left: 5px; } } }

  .profile {
    @include flex($justify: flex-end);
    position: relative;

    .notifications {
      position: relative;
      height: 22px;
      width: 22px;
      @include flex($justify: center);

      .dot {
        position: absolute;
        right: 0;
        top: 0;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: #FF0000; }

      img {
        width: 80%; } }

    .profile-details {
      @include flex($justify: center);
      margin-left: 1rem;

      .profile-image {
        height: 50px;
        width: 50px;
        border-radius: 50%;
        object-fit: cover; }

      .others {
        text-align: right;
        margin: 0 10px;

        .name {
          font-size: 16px;
          font-weight: 500;
          color: $light; }

        .greet {
          font-size: 14px;
          font-weight: 400;
          color: #dcdcdc; } } } } }

/**********    body    **********/
.all-pages {
  width: 80%; }

/**********    side-nav    **********/
.side-nav {
  background-color: rgba($dark, .45);
  border-radius: 20px;
  position: sticky;
  top: 80px;
  width: 18%;
  margin-right: 2%;

  .side-nav-bar {
    position: relative;

    .indic {
      position: absolute;
      left: 0;
      top: 0;
      height: 43px;
      border-radius: 10px;
      width: 2px;
      background-color: $pri;
      @include trans(); }

    .nav-links {
      height: 43px;
      position: relative;
      @include flex($justify: flex-start);
      margin: 10px 0;
      cursor: pointer;
      border-radius: 10px;
      padding: 2rem 0;

      &:hover {
        background-color: rgba($dark, .2); }

      &.active {
        background-color: rgba($dark, .2);
        .icon {
          svg {
            path {
              fill: $pri; } } }
        .name {
          color: $pri; } }

      .icon {
        height: 24px;
        width: 24px;
        @include flex($justify: center);
        margin: 0 15px;

        svg {
          height: 24px;
          width: 24px;

          path {
            @include trans(); } } }

      .name {
        font-size: 18px;
        color: $light;
        @include trans(); } } } }

/**********    homepage    **********/
.homepage {
  background-color: rgba($light, .75);
  backbrop-filter: blur(25px);
  border-radius: 20px;
  width: 100%; }

/**********    banner    **********/
.banner {
  width: 100%;
  position: relative;

  .mySwiper {
    width: 100%;

    .swiper-slide {
      width: 100%; } }

  .slider {
    width: 100%;

    .image {
      height: 350px;
      width: 100%;
      border-radius: 20px;
      overflow: hidden;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba($dark, .4);
        z-index: 1; }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover; }

      .content {
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        @include flex($justify: flex-start);
        flex-direction: column;
        align-items: flex-start;
        color: $light;
        z-index: 3;

        h4 {
          font-size: 18px;
          font-weight: 600;
          line-height: 43px;
          line-height: 10px;
 }          // text-shadow: 0 1px 3px rgba($light, .9)

        h1 {
          font-size: 36px;
          font-weight: 500;
          max-width: 300px;
          margin-bottom: 1rem;
 }          // text-shadow: 0 1px 3px rgba($light, .9)

        p {
          font-size: 18px;
          max-width: 400px;
          margin-bottom: 30px;
 } } } } }          // text-shadow: 0 1px 3px rgba($light, .9)

/**********    feature    **********/
.feature {
  .card-list {
    @include grid($columns: 1fr 1fr 1fr);

    .card {
      padding: 1rem;
      position: relative;
      background-color: $light;
      padding-top: 5rem;
      margin-top: 10rem;
      border-radius: 20px;
      box-shadow: 0px 4px 17px rgba($dark, 0.10);

      img {
        position: absolute;
        left: 50%;
        top: -20%;
        transform: translate(-50%, -50%);
        width: 90%;
        height: 200px;
        object-fit: cover;
        border-radius: 20px; }

      .name {
        font-size: 24px;
        margin-bottom: 1rem; }

      .desc {
        font-size: 18px;
        color: $para; } } } }

/**********    how-to-use    **********/
