        p {
          font-size: 24px; } } }

    .procress {
      .step {
        margin-bottom: 2rem;
        .sn {
          font-size: 24px;
          font-weight: 300;
          margin-bottom: 10px; }

        .name {
          font-size: 24px;
          font-weight: 700;
          margin-bottom: 10px; }

        p {
          font-size: 18px;
          margin-bottom: 10px; } } } }

  .image {
    img {
      height: 80%;
      width: 100%;
      object-fit: cover;
      border-radius: 20px; } } }

/**********    earn-with-us    **********/
.earn-with-us {
  @include grid($columns: 1fr 1fr);

  .image {
    img {
      height: 250px;
      width: 100%;
      object-fit: cover;
      border-radius: 20px; } }

  .name {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 15px; }

  ul {
    padding-left: 2rem;
    li {
      margin-bottom: 1rem; } } }

/**********    footer    **********/
footer {
  padding: 1rem;
  background-color: #141414;
  @include flex($justify: space-between);
  margin-top: 12px;
  border-radius: 15px;

  h4 {
    font-size: 18px;
    line-height: 22px;
    color: #C6C6C6;
    font-weight: 400; }

  .social-links {
    @include flex($justify: flex-end);

    .icon {
      height: 24px;
      width: 24px;
      border-radius: 6px;
      background-color: #383838;
      @include flex($justify: center);
      margin-left: 10px;

      i {
        color: #ebebeb;
        font-size: 15px; } } } }

/**********    about-us    **********/
.about-page {
  background-color: $light;
  padding: 2rem;
  border-radius: 20px; }

/**********    page-banner    **********/
.page-banner {
  h4 {
    font-size: 18px;
    font-weight: 700; }

  h2 {
    font-size: 48px;
    font-weight: 400; } }

/**********    full-card    **********/
.full-card {
  @include grid($columns: 1fr 1.2fr);
  margin-top: 2rem;

  &.rotate {
    @include grid($columns: 1.2fr 1fr); }

  img {
    height: 240px;
    width: 100%;
    border-radius: 20px;
    object-fit: cover; }

  .details {
    h3 {
      margin-bottom: 5px;
      font-size: 24px; }

    p {
      font-size: 18px;
      color: $para;
      line-height: 1.5rem;
      text-align: justify; } } }

/**********    custom-input    **********/
.custom-input {
  height: 60px;
  border-radius: 10px;
  position: relative;
  margin: 2rem 0;

  .form-input-label {
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    color: #6F6F6F;
    z-index: 1;
    @include trans();

    &.shrink {
      @include shrinkLabel(); } }

  .input {
    height: 100%;
    width: 100%;
    background-color: $light;
    border: 2px solid #D3D3D3;
    border-radius: 10px;
    font-size: 18px;
    padding-left: 15px;

    &:focus ~ .form-input-label {
      @include shrinkLabel(); } }

  &.custom-text-area {
    height: 150px;

    .input {
      padding-top: .8rem; }

    .form-input-label {
      top: 18%; } } }

/**********    error    **********/
.error {
  position: fixed;
  left: 50%;
  top: 15%;
  transform: translate(-50%, -50%);
  max-width: 600px;
  padding: .5rem 1rem;
  border-radius: 10px;
  background-color: rgba(#ff0000, .8);
  color: $light;
  font-size: 18px;
  text-align: center;
  z-index: 30;
  pointer-events: none; }

/**********    contact-page    **********/
.contact-page {
  padding: 1.5rem;
  background-color: rgba($dark, .36);
  backdrop-filter: blur(25px);
  border-radius: 20px;
  @include grid($columns: 2fr 1fr);
  align-items: flex-start;

  .contact-form {
    padding: 1.5rem;
    background-color: rgba($light, .84);
    border-radius: 20px;

    h3 {
      font-size: 30px;
      margin: 1rem 0 .4rem; } } }

/**********    contact-infos    **********/
.contact-infos {
  padding: 1rem;
  background-color: $main;
  border-radius: 20px;
  color: $light;

  h3 {
    font-size: 24px;
    margin-bottom: .7rem; }

  h5 {
    position: sticky;
    top: {}
    font-size: 18px;
    margin-bottom: .6rem;
    font-weight: 400; } }

/**********    sign-up, login    **********/
.sign-up, .login {
  @include image();
  min-height: 100vh;
  @include flex($justify: center);

  .inner {
    max-width: 420px;
    width: 100%;

    .top-sec {
      @include flex($justify: space-between);
      align-items: flex-end;
      margin-bottom: 5px;

      .back-btn, .home-btn {
        height: 42px;
        width: 42px;
        border-radius: 50%;
        background-color: $dark;
        @include flex($justify: center); } }

    .sign-nav {
      background-color: rgba($dark, .44);
      border-radius: 15px;
      position: relative;
      height: 50px;
      margin-bottom: 5px;

      .indicator {
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 25%;
        height: 80%;
        border-radius: 10px;
        background-color: $main;
        @include trans();
        z-index: 1; }

      ul {
        @include flex($justify: space-around);
        height: 100%;
        position: relative;
        z-index: 2;
        padding: 0;
        margin: 0;
        list-style: none;
        color: $light;
        font-weight: 500;

        li {
          padding: 1rem;
          cursor: pointer;
          font-size: 18px; } } }

    .for-user, .for-rider, .for-vender {
      opacity: 0;
      @include trans();
      transition-delay: 1s; }

    .sign-up-form, .login-form {
      padding: 2rem 0;
      background-color: rgba($dark, .44);
      border-radius: 20px;
      overflow: hidden;

      .form {
        padding: 0 1.5rem; }

      .form-slide {
        width: 200%;
        display: flex;
        @include trans();

        .form {
          width: 50%;
          padding: 0 1.5rem; } }

      h4 {
        font-size: 24px;
        color: $light;
        font-weight: 600;
        margin-bottom: 2rem;
        padding: 0 1.5rem; }

      .form-input {
        height: 40px;
        width: 100%;
        position: relative;
        margin-bottom: 2rem;

        input {
          height: 100%;
          width: 100%;
          font-size: 18px;
          padding-left: 5px;
          color: $light;

          &:focus ~ .form-input-label {
            @include shrinkLabel(); } }

        span {
          position: absolute;
          top: 50%;
          left: 5px;
          transform: translateY(-50%);
          font-size: 16px;
          color: #c6c6c6;
          pointer-events: none;
          @include trans();

          &.shrink {
            @include shrinkLabel(); } }

        .line {
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          height: 2px;
          width: 100%;
          background-color: rgba($light,.26);

          &.glow {
            &::after {
              width: 100%; } }

          &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 2px;
            width: 0%;
            background-color: rgba($light,.7);
            @include trans(); } } }

      .btn-group {
        @include flex($justify: space-between);

        button {
          width: 100%;
          padding: .5rem 1rem;
          background-color: $main;
          font-size: 18px;
          color: $light;
          border-radius: 10px;
          cursor: pointer;
          margin: 0 5px;

          &:hover {
            @include btnHover(); } } }

      .or {
        font-size: 16px;
        text-align: center;
        color: $light;
        position: relative;
        margin: 1.5rem;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(50%);
          height: 1px;
          width: 42%;
          background-color: rgba($light, .26); }

        &::before {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(50%);
          height: 1px;
          width: 42%;
          background-color: rgba($light, .26); } }

      .social-icons {
        max-width: 250px;
        width: 100%;
        margin: 0 auto;
        @include flex($justify: space-between);

        .icon {
          height: 35px;
          width: 35px;
          border-radius: 50%;
          background-color: $main;
          @include flex($justify: center);
          color: $light;
          font-size: 18px;
          cursor: pointer;

          &:hover {
            @include btnHover(); } } }

      .have-account {
        text-align: center;
        color: $light;
        margin-top: 1rem;
        font-size: 16px;

        span {
          cursor: pointer;
          color: $light;
          text-decoration: underline; } } } } }

/**********    blogpage    **********/
.blogpage {
  padding: 2rem;
  background-color: rgba($dark, .26);
  border-radius: 20px;

  .title {
    font-size: 30px;
    font-weight: 600;
    color: $light;
    margin-bottom: 2rem; }

  .blog {
    .blog-list {
      @include grid($columns: 1fr 1fr 1fr);

      .blog-item {
        border-radius: 20px;
        overflow: hidden;
        background-color: $light;
        @include trans();

        &:hover {
          box-shadow: 0 4px 10px rgba($dark, .2);
          .image {
            img {
              transform: translateX(0) scale(1);
              transition: .45s cubic-bezier(0,0,0,1); } }

          .content {
            .blog-title {
              color: $main; } } }

        .image {
          height: 200px;
          width: 100%;
          border-radius: 10px;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: translateX(-30px) scale(1.3); } }

        .content {
          padding: 1rem 1rem 1.5rem;
          border-radius: 20px;

          .date {
            color: rgba($dark,.6);
            font-size: 12px;
            font-weight: 400;
            margin-bottom: 10px; }

          .blog-title {
            font-size: 18px;
            font-weight: 600;
            color: $dark; } } } } } }

/**********    map    **********/
.map {
  height: 400px;
  width: 100%; }

/**********    notification-page    **********/
.notification-page {
  padding: 2rem;
  background-color: $light;
  border-radius: 20px;

  .title {
    margin-bottom: 1rem; }

  .notifications-list {
    @include flex($justify: flex-start);
    flex-direction: column;

    .item {
      @include grid($columns: .1fr 5fr);
      padding: 1.5rem 0;
      align-items: flex-start;
      border-bottom: 1px solid rgba($dark, .2);

      &:nth-last-child(1) {
        border-bottom: none; }

      .icon {
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: rgba($main, .3);
        @include flex($justify: center);

        svg {
          width: 60%;
          path {
            fill: $main; } } }

      .details {
        .desc {
          font-size: 18px;
          color: $para;
          line-height: 1.7rem;
          margin-bottom: .6rem;
          text-align: justify; }

        .date {
          font-size: 14px;
          color: rgba($dark, .5);
          font-weight: 400; } } } } }

.solutions {
  .title {
    font-size: 30px;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: 600; }

  .mySwiper {
    padding-bottom: 2rem; }

  .item {
    height: 200px;
    width: 100%;
    position: relative;
    border-radius: 20px;
    overflow: hidden;

    &:hover {
      .image {
        transform: scale(1) translateX(0); }

      .details {
        p {
          max-height: 50px;
          opacity: 1;
          pointer-events: auto; } }

      &::after {
        background-image: linear-gradient(180deg, rgba($dark, .1), rgba($dark, .8)); } }

    .image {
      height: 100%;
      width: 100%;
      @include image();
      transform: scale(1.3) translateX(-8px);
      @include trans(); }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-image: linear-gradient(180deg, rgba($dark, .0), rgba($dark, .6));
      z-index: 1;
      @include trans(); }

    .details {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: .5rem;
      color: $light;
      z-index: 2;

      .name {
        font-size: 18px;
        margin-bottom: .4rem; }

      p {
        max-height: 0;
        overflow-y: auto;
        height: 100%;
        font-size: 14px;
        opacity: 0;
        pointer-events: none;
        @include trans(); } } } }

.message-page {
  background-color: #323232;
  height: 100vh;
  padding: 1rem 10px;
  @include flex($justify: space-between);
  align-items: flex-start;
  flex-direction: column;

  .chat-head {
    @include flex($justify: flex-start);
    color: $light;
    margin-bottom: 1rem;

    .user-image {
      height: 40px;
      width: 40px;
      margin: 0 10px;

      img {
        height: 100%;
        width: 100%;
        border-radius: 50%;
        object-fit: cover; } }

    .user-name {
      font-size: 16px;
      font-weight: 500; }

    .status {
      font-size: 12px;
      font-weight: 400; } }

  .message-input {
    width: 100%;
    height: 40px;
    background-color: #6F6F6F;
    border-radius: 4px;
    @include flex($justify: space-between);
    margin-top: 1rem;

    input {
      width: 100%;
      height: 100%;
      padding: 0 1rem;
      font-size: 15px;
      color: $light;

      &::placeholder {
        color: rgba($light, .8); } }

    .send-message-btn {
      padding: .1rem .6rem;
      font-size: 16px;
      font-weight: 600;
      text-transform: uppercase;
      color: #e7e7e7; } } }

.message-section {
  max-height: 80vh;
  height: 100%;
  width: 100%;
  overflow-y: scroll; }

.message-layout {
  width: 100%;
  @include flex($justify: flex-end);
  flex-direction: column;

  .others-message, .your-message {
    width: 100%;
    @include flex($justify: flex-start);
    align-items: flex-end;
    margin-bottom: 10px;

    .user-image {
      height: 30px;
      width: 30px;
      border-radius: 50%;
      @include image();
      margin-right: 5px;

      &.noImage {
        display: none; } }

    .message-box {
      padding: .5rem;
      border-radius: 10px 10px 10px 0;
      background-color: #494949;
      max-width: 70vw;

      &.my-message {}

      .text-message {
        color: $light;
        font-size: 14px; }

      .send-time {
        margin-top: 5px;
        color: rgba($light, .7);
        font-size: 10px; } } }

  .your-message {
    width: 100%;
    @include flex($justify: flex-end);

    .message-box {
      border-radius: 10px 10px 0 10px;
      background-color: $main; } } }
